var test = require('ava')


function swap (arr) {


    for ( var i = 0, begin = 0, piv = arr.length -1 ; i < arr.length - 1  ; i ++) {

        console.log(arr, arr[piv], i , begin);
        if ( arr[i] <= arr[piv]){
            begin = i
            begin++
        }
        else {
            var cur = arr[begin]
            var swappy = arr[piv - 1 ]
            arr[begin] = swappy
            arr[piv - 1] = arr[piv]
            arr[piv] = cur
            piv--
            // piv -= 1
        }
        console.log(arr);
    }
    return arr
}

test('swaps pivot to correct point', t => {

    var arr = [ 1, 8, 2, 9, 7, 4 ]
    var act = swap(arr)
    var exp = [ 1, 2, 4, 9, 7, 8]

    t.deepEqual(act, exp)

    var arr2 = [  8, 2, 9, 7, 4]
    var act2 = swap(arr2)
    var exp2 = [  2, 4, 9, 7, 8]

    t.deepEqual(act2, exp2)

    var arr2 = [  9, 7, 4]
    var act2 = swap(arr2)
    var exp2 = [  4, 7, 9]

    t.deepEqual(act2, exp2)
})
